@import url(https://fonts.googleapis.com/css?family=Roboto:500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/***
**** GENERAL SETTINGS
***/

:root {
  --background-color: #4c2f1b;  
  --secondary-color: #e8eadff6;
  --secondary-color-transparent: #fff9eba1;
  --accent-color: #055a05;
  --text-color-title: #fdfff4f6;
  --text-color: black; 

  --h1-size: 50px;
  --h3-size: 20px;
  --h4-size: 16px;
}

.App * {
  box-sizing: border-box;
}

.App {
  text-align: center;
  padding: 2% 5%;
  position: relative;
  background-color: #4c2f1b;
  background-color: var(--background-color);
}

.App::before {
  content: "";
  background-image: url(/meet/static/media/loginview.f115e4d8.jpg);
  background-size: cover;
  opacity: 0.55;
  position: fixed;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
}

h1 {
  position: relative;
  font-size: 50px;
  font-size: var(--h1-size);
  color: #fdfff4f6;
  color: var(--text-color-title);
}

h3 {
  font-size: 20px;
  font-size: var(--h3-size);
  color: #fdfff4f6;
  color: var(--text-color-title);
}

h4 {
  position: relative;
  font-size: 16px;
  font-size: var(--h4-size);
  color: #fdfff4f6;
  color: var(--text-color-title);
}

p {
  margin: 5px 0;
}

.Alert {
  position: relative !important;
  top: 0px !important;
  font-weight: bold;
  background-color: #fff9eba1;
  background-color: var(--secondary-color-transparent);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  border-width: 3px;
  border-radius: 4px;
}

