/***
**** APP 
***/

/* Logo */
img {
  position: relative;
}

/* CitySearch Component */

.city {
  width: 250px;
  background-color: #e8eadff6;
  background-color: var(--secondary-color);
}

.CitySearch {
  display: flex;
  flex-direction: column;
  flex-basis: 300px;
  max-width: 300px;
  margin: auto;
  position: relative;
}

.suggestions {
  width: 250px;
  z-index: 2;
  align-self: center;
  margin: 0;
  top: 43px;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

ul {
  padding: 0;
  list-style: none;
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #e8eadff6;
  background-color: var(--secondary-color);
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #bcdcdb;
}

/* NumberOfEvents Component */

.NumberOfEvents {
  position: relative;
}

.numberOfEvents {
  width: 200px;
  margin: 20px auto;
  text-align: center;
}

.number-of-events {
  background-color: #e8eadff6;
  background-color: var(--secondary-color);
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
  margin-top: 5px;
}

input[type="number"] {
  width: 150px;
}

/* EventGenre Component */

.data-vis-wrapper {
  display: flex;
  flex-direction: row;
}

.recharts-surface {
  overflow: visible;
}

.recharts-surface {
  background-color: #fff9eba1;
  background-color: var(--secondary-color-transparent);
}

line {
  stroke: black;
  stroke: var(--text-color);
}

.recharts-cartesian-axis-line {
  stroke: black;
  stroke: var(--text-color);
}

.recharts-symbols {
  fill: #055a05;
  fill: var(--accent-color);
}

tspan {
  font-weight: 700;
  color: black;
  color: var(--text-color);
  fill: black;
  fill: var(--text-color);
}

/* EventList Component */

/* Event Component */

.event {
  padding: 10px 10px 10px;
  margin-bottom: 10px;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: relative;
  background-color: #e8eadff6;
  background-color: var(--secondary-color);
}

.show-details-button-div {
  text-align: right;
}

.show-details-button {
  margin: 0;
  background-color: #055a05;
  background-color: var(--accent-color);
  border-style: none;
  border-radius: 3px;
  padding: 5px 10px;
  font-weight: bold;
  color: #e8eadff6;
  color: var(--secondary-color);
}

.show-details-button:hover {
  background-color:rgb(53, 99, 53);
  cursor: pointer;
}

.WelcomeScreen {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  background: #4c2f1b;
  left: 0px;
}

.welcome-spacer {
  height: 10px;
}

.google-icon-wrapper {
